import React, { Component } from 'react'
import { Header, Icon, Table } from 'semantic-ui-react'

const axios = require('axios').default;

class TestsAPI extends Component {
  state = {
    error: false,
    result: false,
    status: false,
    valid: false,
  }
  constructor(props) {
    super(props)
    this.interval = setInterval(this.tick.bind(this), 15000)
    this.tick()
  }
  componentDidUpdate(prevProps) {
    // If the chainId changes in state, reconfigure anchor-link for that chain
    if (this.props.chain.chainId !== prevProps.chain.chainId) {
        this.tick()
    }
  }
  componentWillUnmount() {
    clearInterval(this.interval)
  }
  tick() {
    const t = this
    const { chain } = this.props
    const [endpoint] = chain.rpcEndpoints
    const url = `${endpoint.protocol}://${endpoint.host}`
    axios.get(url)
      .then(function (response) {
          console.log(response)
        t.setState({
          result: response.data,
          status: response.status,
          valid: true,
        })
      })
      .catch(function (error) {
        console.log(error)
        t.setState({
          error,
          valid: false,
        })
      })
  }
  render() {
    const { error, result, status, valid } = this.state;
    const { chain } = this.props
    const [endpoint] = chain.rpcEndpoints
    const url = `${endpoint.protocol}://${endpoint.host}`
    return (
      <Table.Row>
        <Table.Cell textAlign="right">
          API Server:
        </Table.Cell>
        <Table.Cell textAlign="center">
          <Icon
            color={valid ? 'green' : 'red'}
            name={valid ? 'square check' : 'window close'}
            size="large"
          />
          <p>{status}</p>
        </Table.Cell>
        <Table.Cell>
          {valid ? (
              <Header size="small">
                  Connected to {url} ({result.head_block_time})
                  <Header.Subheader>
                      {result.chain_id}
                  </Header.Subheader>
              </Header>
          ) : JSON.stringify(error)}
        </Table.Cell>
      </Table.Row>
    );
  }
}

export default TestsAPI;
